import React from 'react';

interface MainTemplateProps {
  header: React.ReactNode;
  content: React.ReactNode;
  footer?: React.ReactNode;
  className?: string;
}

export const MainTemplate = ({
  header, content, footer, className,
}: MainTemplateProps) => (
  <div className={`relative flex h-full min-h-screen flex-col ${className}`}>
    <header className="absolute z-20 h-fit w-full px-[16px] pt-12 xl:px-28">
      {header}
    </header>
    <main className="flex h-full min-h-screen grow justify-center">
      {content}
    </main>
    {footer && (
    <footer className="h-fit">
      {footer}
    </footer>
    )}
  </div>
);
