import { Icon } from 'components/atoms/icon/icon';
import { MainLogo } from 'components/atoms/main-logo/main-logo';
import { RoundButton } from 'components/atoms/round-button/round-button';
import { CloseButton } from 'components/molecues/close-button/close-button';
import { MetaDataKeys, ItemType } from 'domain/equipment/cart';
import { Link } from 'gatsby';
import { useCart } from 'infrastructure/equipment/use-cart';
import React, { useCallback, useRef } from 'react';

const NavbarItem = ({
  closeNavbar,
  children,
  ...restProps
}: React.LiHTMLAttributes<HTMLLIElement> & { closeNavbar: () => void }) => (
  // eslint-disable-next-line react/jsx-props-no-spreading
  <li {...restProps}><button type="button" onClick={closeNavbar} className="uppercase text-sm font-extrabold">{children}</button></li>
);

export const Navbar = () => {
  const menuRef = useRef<HTMLDivElement>(null);
  const onClosed = useCallback(() => {
    menuRef.current?.setAttribute('data-open', 'false');
  }, []);
  const onOpened = useCallback(() => {
    menuRef.current?.setAttribute('data-open', 'true');
  }, []);

  /** @todo refine the way of calculating total items count */
  const { getFilteredBy, getUniqueTagValues } = useCart();
  const cartItemsCount = getUniqueTagValues(MetaDataKeys.BUILD_CART_ID).length + getFilteredBy({
    exclude: [
      { [MetaDataKeys.TYPE]: ItemType.EQUIPMENT },
    ],
  }).length;

  return (
    <div className="relative flex text-main-white">
      <div className="w-full">
        <RoundButton className="md:hidden" onClick={onOpened}>
          <Icon name="Hamburger" className="h-4 w-4 stroke-main-white" />
        </RoundButton>
        <div className="fixed top-0 left-0 z-50 flex h-full w-full translate-x-full flex-col bg-background-dark p-12 transition-all data-[open=true]:translate-x-0 md:static md:block md:translate-x-0 md:bg-transparent md:p-0 md:transition-none" ref={menuRef}>
          <div className="flex justify-end md:hidden">
            <CloseButton className="h-4" onClick={onClosed} />
          </div>
          <ul className="flex flex-col md:flex-row md:gap-8 lg:gap-16">
            <NavbarItem closeNavbar={onClosed}><Link state={{ buildModal: true }} to="/">zbuduj pc</Link></NavbarItem>
            <NavbarItem closeNavbar={onClosed}><Link to="/o-nas">o nas</Link></NavbarItem>
            <NavbarItem closeNavbar={onClosed}><Link to="/kontakt">kontakt</Link></NavbarItem>
            <NavbarItem closeNavbar={onClosed}><Link to="/faq">FAQ</Link></NavbarItem>
          </ul>
        </div>
      </div>
      <div className="flex w-full justify-end">
        <Link to="/koszyk" className="relative h-full w-min">
          {cartItemsCount > 0 && (
          <div className="absolute top-0 left-1/2 flex h-5 w-5 -translate-y-1/2 items-center justify-center rounded-full bg-main-purple">
            <span className="h-fit w-fit text-base font-semibold">{cartItemsCount}</span>
          </div>
          )}
          <Icon className=" h-5 w-5 fill-main-white " name="Cart" />
        </Link>
      </div>
      <div className="absolute top-0 left-1/2 h-full -translate-x-1/2 md:-translate-y-1/2">
        <Link to="/"><MainLogo /></Link>
      </div>
    </div>
  );
};
