export const enum MetaDataKeys {
  TYPE = 'type',
  BUILD_ID = 'build_id',
  BUILD_CATEGORY = 'build_category',
  BUILD_CART_ID = 'build_cart_id',
  COVER_IMAGE = 'cover_image',
}

export const enum ItemType {
  'GROUPED_EQUIPMENT' = 'GROUPED_EQUIPMENT',
  'EQUIPMENT' = 'equipment',
  'ACCESSORY' = 'accessory',
  'ADDITIONAL_SERVICE' = 'additional_service',
}

export type MetaData<ImageFile> = {
  [MetaDataKeys.TYPE]: ItemType,
  [MetaDataKeys.BUILD_ID]?: string,
  [MetaDataKeys.BUILD_CATEGORY]?: string,
  [MetaDataKeys.BUILD_CART_ID]?: string,
  [MetaDataKeys.COVER_IMAGE]: ImageFile,
}

export interface CartItemDTO<ImageFile> {
  id: string,
  groupTags: MetaData<ImageFile>,
  price: number,
  priceId: string,
  name: string,
}

export interface CartDTO<ImageFile> {
  id: string;
  updateTimestamp: Date;
  items: readonly CartItemDTO<ImageFile>[];
}

export type CartAction<ImageFile> =
  | { type: 'ADD_TO_CART', items: CartItemDTO<ImageFile>[], replace?: boolean}
  | { type: 'REMOVE_FROM_CART', id: string }
  | { type: 'LOAD_CART', localCart: CartDTO<ImageFile> }
  | { type: 'RESET_CART' }
